<!-- , photoswipe -->
<template>
    <!-- v-transfer-dombody, vue -->
    <div class="pr-photo-swipe" v-transfer-dom :data-transfer="true">
        <!-- PhotoSwipe. -->
        <div class="pswp" tabindex="-1" role="dialog" aria-hidden="true" ref="photoSwipe">
            <!--  -->
            <div class="pswp__bg"></div>
            <!--  -->
            <div class="pswp__scroll-wrap">

                <!--  -->
                <div class="pswp__container">
                    <div class="pswp__item"></div>
                    <div class="pswp__item"></div>
                    <div class="pswp__item"></div>
                </div>
                <!-- , UI,  -->
                <div class="pswp__ui pswp__ui--hidden">
                    <div class="pswp__top-bar">
                        <!--  -->
                        <div class="pswp__counter"></div>
                        <!--  -->
                        <button class="pswp__button pswp__button--close" title=""></button>
                        <!--  -->
                        <button class="pswp__button pswp__button--fs" title=""></button>
                        <!--  -->
                        <button class="pswp__button pswp__button--zoom" title=""></button>
                        <!--  http://codepen.io/dimsemenov/pen/yyBWoR -->
                        <div class="pswp__preloader">
                            <div class="pswp__preloader__icn">
                                <div class="pswp__preloader__cut">
                                    <div class="pswp__preloader__donut"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!--  -->
                    <button class="pswp__button pswp__button--arrow--left" title="">
                    </button>
                    <!--  -->
                    <button class="pswp__button pswp__button--arrow--right" title="">
                    </button>
                    <!--  -->
                    <div class="pswp__caption">
                        <div class="pswp__caption__center"></div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import 'photoswipe/dist/photoswipe.css'
    import 'photoswipe/dist/default-skin/default-skin.css'

    import PhotoSwipe from 'photoswipe/dist/photoswipe'
    import PhotoSwipeUI_Default from 'photoswipe/dist/photoswipe-ui-default'
    import TransferDom from 'iview/src/directives/transfer-dom'

    export default {
        name: 'pr-photo-swipe',
        directives: {
            TransferDom
        },
        props: {
            // , 0, 
            index: {
                default: 0
            },
            // 
            items: {
                default: () => {
                    return [
                        // {
                        //     title: '',
                        //     src: '',
                        //     msrc: '',
                        //     // 
                        //     w: '',
                        //     h: ''
                        // }
                    ]
                }
            },
            // 
            size: {
                default: () => {
                    return [1920, 1080]
                }
            },
            // 
            history: {
                default: false
            }
        },
        data() {
            return {
                // 
                el: {},
                // 
                pswpItems: [],
                // 
                options: {
                    index: 0
                },
                // 
                pswpInstance: {}
            }
        },
        methods: {
            // 
            init() {
                this.el = this.$refs['photoSwipe']
            },
            // 
            open(index) {
                let len = this.pswpItems.length
                if (index <= -1 || index > len) {
                    console.warn('')
                    return
                }
                if (len == 0) {
                    console.error('PhotoSwipe, ')
                    return
                }
                // 
                let oImg = new Image()
                // 
                oImg.src = this.pswpItems[index]['src']
                oImg.onload = oImg.onerror = (e) => {
                    if (e.type == 'error') {
                        console.warn('.')
                    } else {
                        this.pswpItems[index]['w'] = oImg.width
                        this.pswpItems[index]['h'] = oImg.height
                        this.load(index)
                    }
                }
            },
            load(index) {
                // 
                this.options.index = index
                this.options.history = this.history
                // 
                this.pswpInstance = new PhotoSwipe(this.el, PhotoSwipeUI_Default, this.pswpItems, this.options)
                // 
                this.pswpInstance.init()
            }
        },
        watch: {
            // 
            items(n) {
                let items = []
                // 
                n.forEach((value) => {
                    let obj = value
                    if (!value['w']) {
                        obj['w'] = this.size[0]
                    }
                    if (!value['h']) {
                        obj['h'] = this.size[1]
                    }
                    items.push(obj)
                })
                this.pswpItems = items
            }
        },
        mounted() {
            this.init()
        }
    }
</script>
<style lang="less" scoped>
    .pswp__caption__center {
        text-align: center;
    }
</style>


